import {AfricleanHttpInterventionProvider} from '../../../../providers/africlean-http/africlean-http-intervention.provider';
import {AfricleanObservablesProvider} from '../../../../providers/africlean-observables/africlean-observables.provider';
import {AfricleanHttpUserProvider} from '../../../../providers/africlean-http/africlean-http-user.provider';
import {ClientType, InterventionType} from '../../../../providers/utils/commonEnum';
import {AfricleanSessionProvider} from "../../../../providers/africlean-session/africlean-session.provider";

@Component({
    selector: 'app-service-managemment',
    templateUrl: './service-managemment.page.html',
    styleUrls: ['./service-managemment.page.scss'],
})
export class ServiceManagemmentPage implements OnInit {

    public planning: string;

    public comments: string;

    public planningCheck = false;

    public interventionCheck = false;


    public commentsCheck = false;

    private userDashboed = false;

    calendar = {
        mode: 'month',
        currentDate: new Date(),
    };


    /**
     * numÃ©ro des pages
     */
    public positions = [];
    /**
     * numÃ©ro des pages
     */
    public positionsClients = [];

    /**
     * Position de la page courante
     */
    @Input() public currentPage = 1;

    /**
     * Subscribes temporaires
     */
    public interventionsTmp: any[];
    public interventions: any[];

    /**
     * La limite pour rÃ©cuperer les donnÃ©es
     */
    public limit = 10;

    /**
     * DonnÃ©es Ã  ignorer
     */
    public skip = 0;

    /**
     * Le nombre de clients par pages
     */
    @Input() public nbrInterventionsPerPage = 5;

    public showAddFormuler = false;

    /**
     * Liste des clients
     */
    public clients: any[] = [];


    public positionsClientPlanning = [];

    /**
     * Position de la page courante
     */
    public currentPageClient = 1;

    /**
     * Annonces temporaires
     */
    public clientsTmp: any[] = [];

    /**
     * La limite pour rÃ©cuperer les donnÃ©es
     */
    public limitClient = 14;

    /**
     * DonnÃ©es Ã  ignorer
     */
    public skipClient = 0;

    /**
     * Le nombre d'annonces par pages
     */
    public nbrClientsPerPage = 7;


    /**
     * Liste des clients
     */
    public clientsPlanning: any[] = [];

    /**
     * Position de la page courante
     */
    public currentPageClientPlanning = 1;

    /**
     * Annonces temporaires
     */
    public clientsTmpPlanning: any[] = [];

    /**
     * La limite pour rÃ©cuperer les donnÃ©es
     */
    public limitClientPlanning = 14;

    /**
     * DonnÃ©es Ã  ignorer
     */
    public skipClientPlanning = 0;

    /**
     * Le nombre d'annonces par pages
     */
    public nbrClientsPerPagePlaning = 7;


    public customerType = 2;


    constructor(private interventionProvider: AfricleanHttpInterventionProvider,
                private navCtrl: NavController,
                private actionSheetCtrl: ActionSheetController,
                private modalCtrl: ModalController,
                private session: AfricleanSessionProvider,
                private userProvider: AfricleanHttpUserProvider,
                private observableProvider: AfricleanObservablesProvider) {

        // this.markDisabled(new Date());
    }

    ngOnInit() {
      //  this.getInterventionsByFilter();
        /*this.observableProvider.loadIntervention.subscribe(
            (loadIncident) => {
                if (loadIncident) {
                    this.showAddFormuler = false;
                    this.limit = 10;
                    this.skip = 0;
                    this.interventions = [];
                    this.interventionsTmp = [];
                    this.positions = [];
                    this.getInterventionsByFilter();
                }
            });*/
    }

    /**
     * Switch vers le planning
     * @param event evenement
     */
    public togglePlanning(event: any) {
        this.planningCheck = event.detail.checked;
    }

    /**
     * Switch vers les commentaires
     * @param event Ã©vÃ©nement
     */
    public toggleComments(event: any) {
        this.commentsCheck = event.detail.checked;
    }

    public toggleIntervention(event: any) {
        this.interventionCheck = event.detail.checked;
    }

    public toggleUser(event: any) {
        this.userDashboed = event.detail.checked;

    }


    sendPosition(position) {
        this.currentPage = position;
        if (position % 2 === 0 && !this.positions.includes(position)) {
            this.positions.push(position);
            this.skip = this.skip + this.limit;
            this.getInterventionsByFilter();
        }
    }


    sendPositionClient(position) {
        this.currentPageClient = position;
        if (position % 2 === 0 && !this.positionsClients.includes(position)) {
            this.positionsClients.push(position);
            this.skipClient = this.skipClient + this.limitClient;
            this.getClientsList(this.customerType, 'position');
        }
    }
    sendPositionClientPlanning(position) {
        this.currentPageClientPlanning = position;
        if (position % 2 === 0 && !this.positionsClientPlanning.includes(position)) {
            this.positionsClientPlanning.push(position);
            this.skipClientPlanning = this.skipClientPlanning + this.limitClientPlanning;
            this.getClientsPlanningList();
        }
    }

    private getInterventionsByFilter() {
        let body: any = {};
        body = {
            limit: this.limit,
            skip: this.skip
        };
        const url = 'intervention/interventions';
        this.interventionProvider.save(url, body).subscribe(
            (interventions) => {
                if (!this.interventions && !this.interventionsTmp) {
                    this.interventions = [];
                    this.interventionsTmp = [];
                    this.positions = [];
                }
                this.interventions = this.interventions.concat(interventions.slice());
                this.interventionsTmp = this.interventionsTmp.concat(interventions.slice());
            }
        );
    }



    private async getClientsByFilter() {
        const user = await this.session.getItem('user');
        const entity =  (user) ? user.entity : null;
        const id = (entity) ? entity.id : null;
        let body: any = {};
        body = {
            limit: this.limitClient,
            skip: this.skipClient,
            customerType: this.customerType,
            entity: id
        };
        this.userProvider.save('client/clients', body).subscribe(
            (clients) => {
                if (!this.clients && !this.clientsTmp) {
                    this.clients = [];
                    this.clientsTmp = [];
                    this.positionsClients = [];
                }
                this.clients = this.clients.concat(clients.slice());
                this.clientsTmp = this.clientsTmp.concat(clients.slice());
                console.log(this.clients);

            }
        );
    }

    getClientsList(event, action) {
        this.clients = (action === 'init') ? [] : this.clients;
        const type = parseInt(event);
        if (type === InterventionType.PRO) {
            this.customerType = InterventionType.PRO;
            console.log(event);
        } else {
            this.customerType = 1;
        }
        this.getClientsByFilter();
    }




    private async getClientsPlanningByFilter() {
        const user = await this.session.getItem('user');
        const entity =  (user) ? user.entity : null;
        const id = (entity) ? entity.id : null;
        let body: any = {};
        body = {
            limit: this.limitClientPlanning,
            skip: this.skipClientPlanning,
            customerType: this.customerType,
            entity: id
        };

        this.userProvider.save('client/clients', body).subscribe(
            (clients) => {
                if (!this.clientsPlanning && !this.clientsTmpPlanning) {
                    this.clientsPlanning = [];
                    this.clientsTmpPlanning = [];
                    this.positionsClientPlanning = [];
                }
               /* this.clientsPlanning = this.clientsPlanning.concat(clients.slice());
                this.clientsTmpPlanning = this.clientsTmpPlanning.concat(clients.slice());*/
            }
        );
    }

    getClientsPlanningList() {
        this.getClientsPlanningByFilter();
    }
}
